











import lausirCodeDialog from '../../components/lausir-codedialog.vue';
export default {
	components: {
		lausirCodeDialog,
	},
	data() {
		return {
			showCodeDialog: false,
			code:"1231"
		}
	},
	onLoad() {

	},
	methods: {
		change:function(res){
			if(res.type == 1){
				this.code = res.code;
				this.showCodeDialog = false;
			}else if(res.type == -1){
				this.code ="请输入验证码";
				this.showCodeDialog = false;
			}else{
				setTimeout(function(){
					res.resendCall()
				},3000)
			}
			
			
		},
		openCodeDialog:function(){
			this.showCodeDialog = true;
		}
	}
}
